<template>
    <div>
        <div class="greeting">Hello {{name}}{{exclamationMarks}}</div>
        <button @click="decrement">-</button>
        <button @click="increment">+</button>
        <input type="text" v-model="name">
        <button @click="submit">submit</button>
    </div>
</template>

<script lang="ts">
import Vue from 'vue'
import { testTypeScript } from '@/utils/index'

export default Vue.extend({
  data () {
    return {
      enthusiasm: 0,
      name: 'sssssssaa'
    }
  },
  methods: {
    increment () { this.enthusiasm++ },
    decrement () {
      if (this.enthusiasm > 1) {
        this.enthusiasm--
      }
    },
    consoleLog (a: boolean, b: object, c: string, ...arg: object[]) {
      console.log(a, b, c, ...arg)
    },
    submit () {
      testTypeScript('111', { name: 222 }, 322)
      // const a = false
      // const e: Error = new Error('Error occurred')
      // this.consoleLog(a, { name: 'test' }, a.toString(), e, 2333)
    }
  },
  computed: {
    exclamationMarks (): string {
      return Array(this.enthusiasm + 1).join('!')
    }
  }
})
</script>

<style>
.greeting {
    font-size: 20px;
}
</style>
